import React, { useEffect, useState } from "react";
import { Heading, Anchor, Accordion, AccordionPanel, Box, Text, TextInput } from "grommet";
import { fetchProducts, fetchStoreById, bid } from "../helpers";
import CardListing from "../components/CardList";
import Loading from "../components/Loading";
import Chat from './Chatroom'
import ReactHtmlParser from 'react-html-parser';
import Parse from '../library/parse'
import { AddCircle, Bundle } from 'grommet-icons'

const Home = ({
  location,
  pathContext,
  pageContext,
  storeName,
  id,
  ...props
}) => {
  const [products, setProducts] = useState([]);
  const [store, setStore] = useState(null);

  useEffect(() => {

    fetchStoreById(id).then((e) => {
      setStore(e);
    });
    fetchProducts(id).then((e) => setProducts(e));

  }, [id]);
  return (
    <>
      {
        !products.length && <Loading />
      }
      <Heading level={3}> { store && store.get('name') } </Heading>
      <Accordion>
        <AccordionPanel label={store && ReactHtmlParser(`<div>${store.get("description")}</div>`)}>
          <Box pad="medium" background="light-2">
            <Chat />
          </Box>
        </AccordionPanel>
      </Accordion>
      <CardListing
        dataList={products.map((e) => ({
          title: e.get("name"),
          thumbnail: e.get('photo') && e.get('photo').url(),
          cta: (
            <>
              <TextInput 
                margin="small"
                reverse={true}
               
              />
              <AddCircle 
                  onClick={() => { bid(e.id, 7) }}
                />
            </>
          ),
        }))}
      />

      
    </>
  );
};

export default Home;
